import { mapMutations } from 'vuex';
export default {
  setup(props) {
    console.log(props);

    function numcut(str) {
      if (str.length > 18) {
        str = str.substr(0, 16) + "...";
      }

      return str;
    }

    return {
      numcut
    };
  },

  props: ["musicList", "subscribedCount"],
  methods: {
    playMusic: function (i) {
      this.updateMusicList(this.musicList);
      this.updateMusicListIndex(i);
    },
    ...mapMutations(['updateMusicList', 'updateMusicListIndex'])
  }
};